@import "./_variables";

.showcaseCard {
  position: relative;
  border: none !important;
  box-shadow: 0 13px 8px -10px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.showcaseCard .imgWrapper {
  cursor: pointer;
  overflow: hidden;
}
.showcaseCard :global(img) {
  height: 160px;
  transition: transform 0.8s ease;
}

.showcaseCard:hover :global(img) {
  transform: scale(1.3);
}
.showcaseCard :global(.card-body) {
  position: relative;
  background-color: rgba(223, 223, 223, 0.1);
}

.showcaseCard :global(.card-title h4) {
  color: $color-primary;
  font-size: 18px;
  font-family: "Open Sans", sans-serif;
}

.showcaseCard :global(.card-title) {
  margin-bottom: 0.5rem;
}

.showcaseCard .badge {
  padding: 0.4rem;
  padding-left: 0.6rem;
  padding-right: 0.6rem;
  margin-right: 0.3rem;
  margin-bottom: 0.1rem;
  font-size: 10px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  color: rgba(25, 34, 74, 1) !important;
  background-color: #e6ebf0 !important;
}

.showcaseCard p {
  font-size: 15px;
}
